html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  box-sizing: border-box;
  font-size: $font-size-root;
}

*,
*::before,
*::after,
input[type="search"] {
  box-sizing: inherit;
}

body {
  font-size: $font-size-base;
  font-family: $font-family-sans-serif;
  line-height: $line-height-base;
}

body,
html {
  height: 100%;
}

blockquote,
dd,
dl,
figure,
h1,
h2,
h3,
h4,
h5,
h6,
p,
pre {
  margin: 0;
}

button {
  background: transparent;
  border: 0;
  padding: 0;
}

button:focus {
  outline: 1px dotted;
  outline: 5px auto -webkit-focus-ring-color;
}

fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

iframe {
  border: 0;
}

ol,
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

//
// Remove `list-style` bullets of nav lists.
//

nav ul {
	list-style: none;
}

//
// Make images responsive.
//

img {
  max-width: 100%;
}

[tabindex="-1"]:focus {
  outline: none !important;
}

//
// Remove delay from tapping on clickable elements.
//

a,
area,
button,
input,
label,
select,
textarea,
[tabindex] {
  -ms-touch-action: manipulation;
  touch-action: manipulation;
}


:root {
  --desktopWidth: $desktop;
  --tabletWidth: $tablet;
  --marginDesktop: $margin-desktop;
  --marginTouch: $margin-touch;
  --fullHd: $fullhd;
}

$fullhd: 1400px !default;
$widescreen: 1280px !default;
$desktop: 1024px !default;
$tablet: 840px !default;
$mobile: 480px !default;
$gutter: 50px !default;
$margin-desktop: 100px !default;
$margin-touch: 25px !default;


@mixin bp($point) {
@if $point == fullhd {
  @media (max-width: $fullhd) { @content; }
}
@if $point == widescreen {
  @media (max-width: $widescreen) { @content; }
}
@if $point == desktop {
  @media (max-width: $desktop) { @content; }
}
@else if $point == tablet {
  @media (max-width: $tablet) { @content; }
}
@else if $point == mobile {
  @media (max-width: $mobile)  { @content; }
}
}

@mixin mw($point) {
@if $point == fullhd {
  @media (min-width: $fullhd + 1px) { @content; }
}
@if $point == widescreen {
  @media (min-width: $widescreen + 1px) { @content; }
}
@if $point == desktop {
  @media (min-width: $desktop + 1px) { @content; }
}
@else if $point == tablet {
  @media (min-width: $tablet + 1px) { @content; }
}
@else if $point == mobile {
  @media (min-width: $mobile + 1px)  { @content; }
}
}


@mixin clamp($minsize, $maxsize) {
$scalesize: getfontvw($maxsize) / 1;
$scalesize: strip-unit($scalesize) + vw;

$clamp-size: #{'min(max(#{$minsize}, #{$scalesize}), #{$maxsize})'};
font-size: $clamp-size;
}

@mixin clampHeight($minsize, $maxsize) {
$scalesize: getfontvw($maxsize) / 1;
$scalesize: strip-unit($scalesize) + vw;

$clamp-size: #{'min(max(#{$minsize}, #{$scalesize}), #{$maxsize})'};
max-height: $clamp-size;
}


@function getfontvw($fontsize) {
$result: 0vw;

$result: ($fontsize / 1600) * 100;

@return $result +  0vw;
}

@function strip-unit($number) {
@if type-of($number) == 'number' and not unitless($number) {
  @return $number / ($number * 0 + 1);
}

@return $number;
}